import { getAdminOrders } from '@/actions/order';
import { getStock } from '@/actions/stock';

export default async function AdminDashboard() {
  const stock = await getStock();
  const recentOrders = await getAdminOrders();

  const totalOrders = recentOrders.length;
  const newOrders = recentOrders.filter(o => o.status === 'NEW').length;
  const doneOrders = recentOrders.filter(o => o.status === 'DONE').length;

  return (
    <div className="space-y-8 animate-in fade-in duration-500">
      <header className="flex justify-between items-end border-b border-neutral-800 pb-4">
        <div>
          <h1 className="text-3xl font-bold">Dashboard</h1>
          <p className="text-neutral-400 mt-1">Ringkasan aktivitas Toro Store</p>
        </div>
      </header>

      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <div className="bg-neutral-900 border border-neutral-800 p-6 rounded-lg pointer-events-none">
          <h3 className="text-neutral-400 text-sm font-medium uppercase tracking-wider mb-2">Total Order</h3>
          <p className="text-4xl font-bold font-sans">{totalOrders}</p>
        </div>
        <div className="bg-neutral-900 border border-blue-900/50 p-6 rounded-lg">
          <h3 className="text-blue-400 text-sm font-medium uppercase tracking-wider mb-2">Order Baru (Pending)</h3>
          <p className="text-4xl font-bold font-sans text-blue-400">{newOrders}</p>
        </div>
        <div className="bg-neutral-900 border border-emerald-900/50 p-6 rounded-lg">
          <h3 className="text-emerald-400 text-sm font-medium uppercase tracking-wider mb-2">Order Selesai</h3>
          <p className="text-4xl font-bold font-sans text-emerald-400">{doneOrders}</p>
        </div>
        <div className="bg-neutral-900 border border-amber-900/50 p-6 rounded-lg">
          <h3 className="text-amber-400 text-sm font-medium uppercase tracking-wider mb-2">Stok Tersedia</h3>
          <p className="text-3xl font-bold font-sans text-amber-500 mt-2">
            B: {stock.basicStock} | P: {stock.premiumStock} | S: {stock.specialStock}
          </p>
        </div>
      </div>

      <div>
        <h2 className="text-xl font-bold mb-4">Aktivitas Terbaru</h2>
        <div className="bg-neutral-900 border border-neutral-800 rounded-lg overflow-hidden">
          <div className="overflow-x-auto">
            <table className="w-full text-left border-collapse min-w-[700px]">
              <thead>
                <tr className="bg-neutral-950/50 border-b border-neutral-800">
                  <th className="p-4 text-sm font-medium text-neutral-400">Order ID</th>
                  <th className="p-4 text-sm font-medium text-neutral-400">Paket</th>
                  <th className="p-4 text-sm font-medium text-neutral-400">Skin/Event</th>
                  <th className="p-4 text-sm font-medium text-neutral-400">Status</th>
                  <th className="p-4 text-sm font-medium text-neutral-400">Waktu</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-neutral-800">
                {recentOrders.slice(0, 5).map((order) => (
                  <tr key={order.id} className="hover:bg-neutral-800/50 transition-colors">
                    <td className="p-4 text-sm font-mono text-neutral-300">{order.orderCode}</td>
                    <td className="p-4 text-sm">{order.packageType}</td>
                    <td className="p-4 text-sm">{order.itemName}</td>
                    <td className="p-4">
                      <span className={`px-2 py-1 rounded text-xs font-bold whitespace-nowrap ${
                        order.status === 'NEW' ? 'bg-blue-500/10 text-blue-400' :
                        order.status === 'CONFIRMED' ? 'bg-amber-500/10 text-amber-400' :
                        order.status === 'DONE' ? 'bg-emerald-500/10 text-emerald-400' :
                        'bg-red-500/10 text-red-500'
                      }`}>
                        {order.status}
                      </span>
                    </td>
                    <td className="p-4 text-sm text-neutral-500 whitespace-nowrap">
                      {order.createdAt.toLocaleDateString('id-ID', { hour: '2-digit', minute: '2-digit' })}
                    </td>
                  </tr>
                ))}
                {recentOrders.length === 0 && (
                  <tr>
                    <td colSpan={5} className="p-8 text-center text-neutral-500">
                      Belum ada order masuk
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  );
}
